import styled from "styled-components";

/*
 * Animation for revealing a section when scrolled onto.. CSS (1st phase) Setup our CSS
 */
const Revealable = styled.section`
  opacity: ${({ show }) => (show ? "100" : "0")};
  margin-top: ${({ show }) => (show ? "0" : "100px")};
  transition: opacity ease-out 1s, margin-top cubic-bezier(0, 0, 0.58, 1) 1s;
`;

export default Revealable;
