import React, { Component } from "react";
import picture from "../Pics/3_bar_menu_icon.png";

class Navbar extends Component {
  constructor() {
    super();
    this.state = {
      width: window.innerWidth,
      isTrue: false
    };
  }

  componentDidMount() {
    window.addEventListener("resize", this.resized());
  }
  componentWillUnmount() {
    window.removeEventListener("resize", this.resized());
  }

  help = () => {
    let inner_width = window.innerWidth;
    this.setState({
      width: inner_width
    });
  };

  resized = () => {
    let inner_width = window.innerWidth;
    if (inner_width >= 720) {
      this.setState({
        isTrue: true
      });
    } else {
      this.setState({
        isTrue: false
      });
    }
    console.log(this.state.isTrue + " :" + inner_width);
  };

  render() {
    const ul_divs = (
      <div className="div_links">
        <ul className="nav_links">
          <a href="#a">
            <li>About Me</li>
          </a>

          <a href="#b">
            <li>Projects</li>
          </a>

          <a href="#c">
            <li>Contact </li>
          </a>
        </ul>
      </div>
    );
    return (
      <div className="nav_container center">
        <nav
          className="nav"
          style={{
            backgroundColor: "tomato",
            position: this.props.links.position,
            marginTop: this.props.links.padding
          }}
        >
          <a href="#d">
            <h1>Anthony Hsia</h1>
          </a>
          {this.state.isTrue ? (
            ul_divs
          ) : (
            <img src={picture} width="50" height="50"></img>
          )}
        </nav>
      </div>
    );
  }
}

export default Navbar;
